import React, { useEffect, useState } from "react";
import Axios from "axios";
import Breadcrumbs from "@mui/material/Breadcrumbs";
import Typography from "@mui/material/Typography";
import Link from "@mui/material/Link";
import NavigateNextIcon from "@mui/icons-material/NavigateNext";
import { useLocation } from "react-router-dom";

const translate = (segment) => {
  switch (segment) {
    case "events":
      return "";
    case "edit":
      return "";
    case "devices":
      return "";
    case "newEvent":
      return " ";
    // Add translations for other segments as needed
    default:
      return segment;
  }
};

function isNumeric(str) {
  return /^\d+$/.test(str);
}

function EventSegment({ eventID }) {
  const [eventData, setEventData] = useState(null);
  useEffect(() => {
    const apiUrl = `https://smart-donate.com/backend/api/getEvents.php?username=admin&password=aDMin1234&event_id=${eventID}`;

    const fetchData = async () => {
      try {
        const response = await Axios.get(apiUrl);
        setEventData(response.data);
      } catch (error) {
        console.error("Error:", error);
      }
    };

    fetchData();
  }, [eventID]);

  return (
    <Typography color="#ffc266" sx={{ fontWeight: "bold" }}>
      {eventData?.name || `Loading...`}
    </Typography>
  );
}

export default function CustomBreadcrumbs() {
  const location = useLocation();
  const pathSegments = location.pathname.split("/").filter(Boolean);

  return (
    <Breadcrumbs
      separator={
        <NavigateNextIcon sx={{ transform: "scaleX(-1)" }} fontSize="small" />
      }
      aria-label="breadcrumb"
      sx={{
        textTransform: "uppercase",
        fontSize: "12px",
        fontFamily: "Noto Kufi Arabic",
        color: "grey",
        padding: "8px 16px",
        boxShadow: "2px 2px 5px rgba(0, 0, 0, 0.3)",
        backgroundColor: "white",
        textAlign: "right",
        direction: "rtl",
        display: "flex", // Ensure items are in the same row
      }}
    >
      {pathSegments.map((segment, index) => (
        <span key={segment}>
          {isNumeric(segment) ? (
            <EventSegment eventID={segment} />
          ) : (
            <>
              <Link
                href={`/${pathSegments.slice(0, index + 1).join("/")}`}
                sx={{
                  fontSize: "15px",
                  textDecoration: "none",
                  color: "grey",
                  "&:hover": { color: "grey", textDecoration: "underline" },
                }}
              >
                {translate(segment)}
              </Link>
              {/* {index < pathSegments.length - 1 && (
                <NavigateNextIcon
                  sx={{ transform: "scaleX(-1)" }}
                  fontSize="small"
                />
              )} */}
            </>
          )}
        </span>
      ))}
    </Breadcrumbs>
  );
}
